<template>
	<div>
		<commonBody>
			<template v-slot:topNavigation>
				<TopNavigation></TopNavigation>
			</template>
			<template v-slot:middle>
				<articleContent class="articleContent"></articleContent>
			</template>
		</commonBody>
	</div>
</template>

<script>
import articleContent from "./articleContent.vue";
export default {
	data() {
		return {
			//文章内容
			articleContent: {},
		};
	},
	methods: {
		// 初始化页面布局
		initLayout() {
			this.$publicFn.initLayoutSinglePage();
		},
	},
	mounted() {
		this.initLayout();
		window.onresize = () => {
			return (() => {
				this.initLayout();
			})();
		};
	},
	components: {
		articleContent,
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";

.articleContent {
	// @extend .widthAndHeightAreFull;
	width: 120%;
	height: 100%;
	background-color: $pageDominantColor;
	border-radius: $borderRadius;
	// 允许滚动，隐藏超出部分，隐藏滚动条
	overflow: auto;
	overflow-x: hidden;
	overflow-y: auto;
	&::-webkit-scrollbar {
		width: 0;
		height: 0;
	}

	&::-webkit-scrollbar-thumb {
		background-color: transparent;
	}
}
</style>
