<template>
	<div class="outerBox">
		<div class="box-left">
			<img
				src="../../assets/image/logo.png"
				@click="this.$publicFn.redirectToRoute('index')"
			/>
			<p class="title" @click="this.$publicFn.redirectToRoute('index')">
				文章阅读网
			</p>
		</div>
		<div class="box-right">
			<el-button
				class="publish"
				@click="this.$publicFn.redirectToRoute('publishAnArticle')"
			>
				<img src="../../assets/image/IconImg/IconAdd.png" />发布
			</el-button>

			<div v-if="!showAvatar" class="loginAndEnroll">
				<el-button
					type="primary"
					@click="this.$publicFn.redirectToRoute('login')"
					>登录</el-button
				>
				<!-- 注册按钮 -->
				<el-button plain @click="this.$publicFn.redirectToRoute('enroll')"
					>注册</el-button
				>
			</div>
			<div v-else class="user">
				<!-- 头像 -->
				<img :src="`${this.$store.state.baseURL}${this.userInfo.avatar}`" />
				<!-- 昵称 -->
				<div>{{ this.userInfo.nickname }}</div>
				<el-button @click="rollOutLogin">退出</el-button>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			//显示头像
			showAvatar: false,
			//用户信息
			userInfo: {},
		};
	},
	methods: {
		//获取用户信息
		async getUserInfo() {
			let value = { requestName: "userInformation", token: "" };
			value.token = this.$publicFn.getToken();
			let res = await this.$http.getUserInfo(value);
			// console.log(res);
			if (res.code == 200) {
				if (res.data.length > 0) {
					this.showAvatar = true;
					this.userInfo = res.data[0];
					// console.log(this.userInfo);
				}
			}
		},
		//推出登录
		rollOutLogin() {
			//清空本地token
			this.$publicFnNaNpxoveToken();
			this.showAvatar = false;
			this.$message({
				message: "退出成功",
				type: "success",
			});
		},
	},
	mounted() {
		this.getUserInfo();
		// console.log(this.$store.state.baseURL);
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
$bothSidesMargins: 5%;
.outerBox {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.box-left {
	// background-color: red;
	display: flex;
	align-items: center;
	margin-left: $bothSidesMargins;
	img {
		width: 6.25rem;
		height: 5rem;
		@include cursor-pointer();
	}
	.title {
		@include cursor-pointer();
		font: {
			size: 1.5625rem;
			weight: 700;
		}
		color: $pageTitleColor;
	}
}
.box-right {
	display: flex;
	align-items: center;
	margin-right: $bothSidesMargins;
	// background-color: red;
	img {
		width: 1.25rem;
		height: 1.25rem;
	}
	.publish {
		margin-right: 0.625rem;
	}
	.user {
		margin-left: 0.625rem;
		display: flex;
		align-items: center;
		img {
			@include cursor-pointer();
			width: 3.125rem;
			height: 3.125rem;
			border-radius: 50%;
		}
		div {
			@include cursor-pointer();
			font: {
				size: 1.25rem;
				weight: 700;
			}
			margin-left: 0.625rem;
			margin-right: 0.625rem;
		}
	}
}
</style>
