<template>
	<div class="outerBox">
		<div class="operateList">
			<div @click="showDialog('nickname')">修改用户名</div>
			<div @click="showDialog('phone')">修改手机号</div>
			<div @click="showDialog('password')">修改密码</div>
			<div @click="showDialog('avatar')">修改头像</div>
		</div>
		<dialog id="nickname">
			<h1>修改用户名</h1>
			<div>原用户名:{{ userInfo.nickname }}</div>
			<div>
				新用户名:<input
					v-model="newUserInfor.nickname"
					placeholder="请输入新用户名"
				/>
			</div>
			<div>
				<el-button @click="closeDialog('nickname')"> 取消 </el-button>
				<el-button @click="confirmTheModification('nickname')">
					确定
				</el-button>
			</div>
		</dialog>
		<dialog id="phone">
			<h1>修改手机号</h1>
			<div>原手机号:{{ userInfo.phone }}</div>
			<div>
				新手机号:<input
					v-model="newUserInfor.phone"
					placeholder="请输入新手机号"
				/>
			</div>
			<div>
				<el-button @click="closeDialog('phone')"> 取消 </el-button>
				<el-button @click="confirmTheModification('phone')"> 确定 </el-button>
			</div>
		</dialog>
		<dialog id="password">
			<h1>修改密码</h1>
			<div>
				新密码:<input
					v-model="newUserInfor.password"
					placeholder="请输入新密码"
				/>
			</div>
			<div>
				确认密码:<input
					v-model="newUserInfor.password2"
					placeholder="请确认密码"
				/>
			</div>
			<div>
				<el-button @click="closeDialog('password')"> 取消 </el-button>
				<el-button @click="confirmTheModification('password')">
					确定
				</el-button>
			</div>
		</dialog>
		<dialog id="avatar">
			<h1>修改头像</h1>
			<el-upload
				class="upload-demo"
				drag
				name="image"
				:action="`${this.$store.state.baseURL}upload`"
				multiple
				:on-success="successUpload"
			>
				<img src="../../assets/image/IconImg/upload.png" class="uploadImg" />
				<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				<div class="el-upload__tip" slot="tip">
					只能上传jpg/png文件,且不超过2Mb
				</div>
			</el-upload>
			<div>
				<el-button @click="closeDialog('avatar')"> 取消 </el-button>
				<el-button @click="confirmTheModification('avatar')"> 确定 </el-button>
			</div>
		</dialog>
	</div>
</template>

<script>
export default {
	data() {
		return {
			// 用户信息
			userInfo: {},
			//修改后的用户名
			newUserInfor: {
				nickname: "",
				phone: "",
				password: "",
				password2: "",
				avatar: "",
			},
		};
	},
	methods: {
		//获取用户信息
		async getUserInfo() {
			let value = { requestName: "mycenter", token: "" };
			value.token = this.$publicFn.getToken();
			let res = await this.$http.getUserInfo(value);
			// console.log(res);
			if (res.code == 200) {
				if (res.data.length > 0) {
					this.userInfo = res.data[0];
				}
			}
		},
		//打开弹出框phone
		showDialog(id) {
			let dialog = document.getElementById(id);
			dialog.showModal();
		},
		//关闭弹窗
		closeDialog(id) {
			let dialog = document.getElementById(id);
			dialog.close();
		},
		//确认修改
		confirmTheModification(id) {
			if (id == "password") {
				let reg =
					/^\S*(?=\S{6,})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/;
				if (!reg.test(this.newUserInfor.password)) {
					this.$message({
						message: "密码最少6位必须包含大小写字母、数字、特殊字符",
						type: "warning",
					});
					return;
				}
				if (this.newUserInfor.password != this.newUserInfor.password2) {
					this.$message({
						message: "两次输入的密码不一致",
						type: "warning",
					});
					return;
				}
			}
			if (id == "phone") {
				let reg = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/;
				if (!reg.test(this.newUserInfor.phone)) {
					this.$message({
						message: "请输入正确的手机号",
						type: "warning",
					});
					return;
				}
			}
			if (id == "nickname") {
				if (this.newUserInfor.nickname == "") {
					this.$message({
						message: "请输入新用户名",
						type: "warning",
					});
					return;
				}
			}
			let value = {
				modifyTheProjectName: id,
				token: "",
				afterModification: "",
			};
			if (id == "avatar") {
				if (this.newUserInfor.avatar == "") {
					this.$message({
						message: "请上传头像",
						type: "warning",
					});
				}
			}
			value.token = this.$publicFn.getToken();
			value.afterModification = this.newUserInfor[id];
			// console.log(value);
			//修改
			this.upDataUser(value, id);
			//成功关闭弹窗,,刷新页面
			//失败提示
		},
		//文件上传成功
		successUpload(res) {
			if (res.code == 200) {
				this.newUserInfor.avatar = res.value;
				// console.log(this.newUserInfor.avatar);
			} else {
				this.$message.error(res.msg);
			}
		},
		//修改请求
		async upDataUser(value, id) {
			let res = await this.$http.upDataUser(value);
			// console.log(res);
			if (res.code == 200) {
				this.$message({
					message: res.msg,
					type: "success",
				});
				//关闭弹窗
				this.closeDialog(id);
				//刷新当前页面
				location.reload();
				if (id == "nickname" || id == "password") {
					this.$publicFn.redirectToRoute("login");
				}
			} else {
				this.$message({
					message: "修改失败",
					type: "warning",
				});
			}
		},
	},
	mounted() {
		this.getUserInfo();
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
$divNumber: 0;
.outerBox {
	padding: 1.25rem 0 0 0.625rem;
	box-sizing: border-box;
	background-color: $pageDominantColor;
	@extend .widthAndHeightAreFull;
	border-radius: $borderRadius;
	@include display-flex($align: left);
	// 设置.operateList中的偶数位置div的样式
	.operateList {
		$divNumber: $divNumber + 1;
		@for $i from $divNumber through 5 {
			@if $i % 2 == 0 {
				div:nth-child(#{$i}) {
					margin: 1.25rem 0;
				}
			}
		}
		div {
			@include cursor-pointer();
		}
	}
	dialog {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		padding: 1.25rem;
		border: 0;
		border-radius: $borderRadius;
		div {
			margin: 0.625rem 0;
			input {
				margin-left: 0.625rem;
			}
		}
		.uploadImg {
			width: 3.125rem;
			height: 3.125rem;
		}
	}
}
</style>
