<template>
	<div class="outerBox">
		<!-- 热门文章 -->
		<div class="popularArticles">
			<div class="title">热门文章</div>
			<template v-for="(item, index) in popularList" :key="index">
				<div class="popularList" v-if="index < showHotContentNumber">
					<div class="itemTitle">{{ item.title }}</div>
					<div class="itemViews">
						{{ $publicFn.convertToMillion(parseInt(item.see_num)) }}
					</div>
				</div>
			</template>
		</div>
		<!-- 联系方式 -->
		<div class="contactInformation">
			<div class="contactTitle">友情链继</div>
			<div class="linksArea">
				<div
					class="contactLIst"
					v-for="(item, index) in selectLinkList"
					:key="index"
				>
					<a :href="item.link" class="itemLink">{{ item.name }}</a>
				</div>
			</div>
			<div class="contactTitle">联系方式</div>
			<div class="linksArea">
				<div
					class="contactLIst"
					v-for="(item, index) in contactList"
					:key="index"
				>
					<p class="itemLink">{{ item.name }}</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name: "rightContent",
	props: {
		showHotContentNumber: {
			type: Number,
			default: 5,
		},
	},
	data() {
		return {
			// 联系方式列表
			contactList: [
				{
					name: "2964142130@qq.com",
				},
				{
					name: "thexuyan@outlook.com",
				},
				{
					name: "www.wenzhangyuedu.com",
				},
			],
			// 热门文章列表
			popularList: [],
			//友情连接列表
			selectLinkList: [],
		};
	},
	methods: {
		//请求热门文章列表
		async getPopularList() {
			let res = await this.$http.selectHotReviewList();
			this.popularList = res.data;
			// console.log(this.popularList);
		},
		//请求友情连接列表
		async getLinkList() {
			let res = await this.$http.selectLink();
			this.selectLinkList = res.data;
		},
	},
	mounted() {
		this.getPopularList();
		this.getLinkList();
	},
	created() {},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
$rightPadding: 0 1.25rem;
@mixin titleSize {
	color: $pageTitleColor;
	padding-top: 0.625rem;
	margin-bottom: 0.625rem;
	font: {
		size: 1.375rem;
		weight: bold;
	}
}
.outerBox {
	@extend .widthAndHeightAreFull;
	box-sizing: border-box;
	position: relative;
	.popularArticles {
		border-radius: $borderRadius;
		@extend .widthAndHeightAreFull;
		background-color: $pageDominantColor;
		height: 58%;
		overflow: hidden;
		padding: $rightPadding;
		// box-sizing: border-box;
		.title {
			// color: $pageTitleColor;
			// padding-top: .625rem;
			// margin-bottom: .625rem;
			@include titleSize;
		}
		.popularList {
			@include display-flex($justify: space-between);
			margin-bottom: 0.9375rem;
			.itemTitle {
				@include cursor-pointer();
				width: 8.75rem;
				@extend .hideText;
				font: {
					size: 1rem;
				}
			}
			.itemViews {
				width: 3.75rem;
				@extend .hideText;

				color: $pageGrayColor;
				// margin-left: .9375rem;
				font: {
					size: 0.625rem;
				}
			}
		}
	}
	.contactInformation {
		border-radius: $borderRadius;
		@extend .widthAndHeightAreFull;
		background-color: $pageDominantColor;
		height: 40%;
		position: absolute;
		bottom: 0;
		padding: $rightPadding;
		@extend .hideText;
		.contactTitle {
			@include titleSize;
		}
		.linksArea {
			height: 6.25rem;
			// background-color: red;
			@extend .hideText;
			.contactLIst {
				// background-color: green;
				@extend .hideText;
				width: 100%;
				font: {
					size: 0.8125rem;
				}
				color: $pageAColor;
				line-height: 1.25rem;
				.itemLink {
					color: $pageAColor;
				}
			}
		}
	}
}
</style>
