<template>
	<div>
		<!-- 分类列表 -->
		<div class="classify">
			<div class="classifyList">
				<template v-for="(item, index) in classifyList">
					<div class="item" v-if="index < showClassifyNumber">
						<div
							class="itemName"
							:class="
								index == classifyIndex
									? 'selectAColor'
									: 'theColorIsNotSelected'
							"
							@click="changeClassify(index, item.id)"
						>
							{{ item.name }}
						</div>
					</div>
				</template>
				<img
					src="@/assets/image/IconImg/IconList.png"
					@click="isShowHideClassify = !isShowHideClassify"
					v-if="classifyList.length > showClassifyNumber"
				/>
			</div>

			<div class="hiddenClassifications" v-if="isShowHideClassify">
				<template v-for="(item, index) in classifyList">
					<div class="item" v-if="index >= showClassifyNumber">
						<div
							class="itemName"
							:class="
								index == classifyIndex
									? 'selectAColor'
									: 'theColorIsNotSelected'
							"
							@click="changeClassify(index, item.id)"
						>
							{{ item.name }}
						</div>
					</div>
				</template>
			</div>
		</div>
		<!-- 文章列表 -->

		<ListOfArticles
			class="listItemStyle"
			v-if="articleListChild.length > 0"
			:articleList="articleListChild"
			:showArticleNumber="showArticleNumber"
		></ListOfArticles>
		<!-- {{articleListChild}} -->
	</div>
</template>

<script>
export default {
	props: {
		showClassifyNumber: {
			type: Number,
			default: 11,
		},
		showArticleNumber: {
			type: Number,
			default: 5,
		},
		articleList: {
			type: Array,
			default: () => [],
		},
	},

	data() {
		return {
			// 文章分类列表
			classifyList: [],
			// 是否显示隐藏的分类选型
			isShowHideClassify: false,
			//分类下标
			classifyIndex: 0,
			//文章列表
			articleListChild: this.articleList,
		};
	},
	methods: {
		//请求文章分类列表
		async getArticleListByClassify() {
			let classifyList = await this.$http.selectCategorizedList();
			this.classifyList = classifyList.data;
		},
		//改变选中的分类
		changeClassify(index, id) {
			// console.log(id);
			this.classifyIndex = index;
			this.isShowHideClassify = false;
			if (id == 1) {
				this.getArticleListByClassify();
			} else {
				this.getArticleListByClassifyId(id);
			}
		},
		//根据分类id获取文章列表
		async getArticleListByClassifyId(id) {
			let value = {
				cate_id: id,
			};
			let res = await this.$http.SelectClassifyToContent(value);

			if (res.code === 200) {
				this.$message.success(res.msg);
				const newArticleList = res.data;
				// console.log(newArticleList);
				// 通过事件将更改后的数据传递回父组件
				this.$emit("updateArticleList", newArticleList);
			} else if (res.code === 100) {
				this.$message.error(res.msg);
				const newArticleList = [];
				// 通过事件将更改后的数据传递回父组件
				this.$emit("updateArticleList", newArticleList);
			}
		},
	},
	mounted() {
		//请求文章分类列表
		this.getArticleListByClassify();
		// console.log(this.$parent.articleList);
	},
	computed: {},
	created() {},
	watch: {
		articleList(newValue, oldValue) {
			// console.log('newValue',newValue);
			this.articleListChild = [];
			this.articleListChild = newValue;
			// console.log('oldValue',oldValue);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
@mixin listStyle {
	width: 100%;
	background-color: $pageDominantColor;
	border-radius: $borderRadius;
	background-color: $pageDominantColor;
	padding: 0 0.625rem;
	box-sizing: border-box;
	.item {
		@include cursor-pointer();
		display: inline-block;
		box-sizing: border-box;
		@extend .hideText;
		.itemName {
			width: 3.125rem;
			margin-right: 0.625rem;
			box-size: border-box;
			display: inline-block;
			line-height: 2.25rem;
			@extend .hideText;
		}
	}
}

.middleArea {
	position: relative;
	width: 100%;
	.classify {
		position: relative;
		margin-bottom: 0.625rem;
		.classifyList {
			// @extend .hideText;
			@include listStyle;
			position: relative;
			height: 2.25rem;
			.selectAColor {
				color: chocolate;
			}
			.theColorIsNotSelected {
				color: black;
			}
			img {
				@include cursor-pointer();
				position: absolute;
				right: 0.625rem;
				top: 50%;
				transform: translateY(-50%);
				width: 0.9375rem;
				height: 0.9375rem;
			}
		}

		.hiddenClassifications {
			@include listStyle;
			position: absolute;
			top: 3.125rem;
			z-index: 999;
			//下边框阴影
			box-shadow: 0 0 0.625rem 0 rgba(0, 0, 0, 0.2);
		}
	}

	//设置listItemStyle的样式
	.listItemStyle {
		// background-color: red;
		// border-radius: $borderRadius;
		position: absolute;
		top: 3.125rem;
		left: 0;
		right: 0;
		bottom: 0.625rem;
		overflow: hidden;
		// z-index: 999;
		//隐藏滚动条样式
		&::-webkit-scrollbar {
			display: none;
		}
	}
}
</style>
