<template>
	<div>
		<common-body>
			<template v-slot:topNavigation>
				<TopNavigation></TopNavigation>
			</template>
			<template v-slot:middle>
				<IntermediateArticleArea
					:showClassifyNumber="showClassifyNumber"
					:showArticleNumber="showArticleNumber"
					:articleList="articleList"
					class="middleArea"
					@updateArticleList="updateArticleList"
				></IntermediateArticleArea>
			</template>
			<template v-slot:left>
				<userInformation></userInformation>
			</template>
		</common-body>
	</div>
</template>

<script>
import userInformation from "./userInformation.vue";
export default {
	data() {
		return {
			//显示分类的个数
			showClassifyNumber: 11,
			//显示文章的个数
			showArticleNumber: 5,
			//文章列表
			articleList: [],
		};
	},
	methods: {
		// 初始化页面布局
		initLayout() {
			this.$publicFn.initLayoutSinglePage().then(res => {
				this.showClassifyNumber = res.showClassifyNumber;
				this.showArticleNumber = res.showArticleNumber;
			});
		},
		//请求我的发布
		async getMyRelease() {
			let value = { name: "mycontent", token: "" };
			value.token = this.$publicFn.getToken();
			let res = await this.$http.myVariousArticles(value);
			// console.log(res);
			this.articleList = res.data;
		},
		//根据分类id获取文章列表
		updateArticleList(newArticleList) {
			let temp = [];
			//将this.articleList和newArticleList中相同id的文章放入temp中
			for (let i = 0; i < this.articleList.length; i++) {
				for (let j = 0; j < newArticleList.length; j++) {
					if (this.articleList[i].id == newArticleList[j].id) {
						temp.push(this.articleList[i]);
					}
				}
			}
			if (temp.length == 0) {
				this.$message.error("没有找到相关文章");
				this.articleList = [];
				return;
			}
			this.articleList = [];
			this.articleList = temp;
			// console.log("this.articleList新的", this.articleList);
			// this.articleList = newArticleList;
			// console.log('this.articleList', this.articleList);
		},
	},
	mounted() {
		this.initLayout();
		window.onresize = () => {
			return (() => {
				this.initLayout();
			})();
		};
		this.getMyRelease();
	},
	components: {
		userInformation,
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
@import "@/assets/scss/publicPropertyValues.scss";
.middleArea {
	// width: $pageWidth !important;
	// height: 100%;
}
.personalCenter {
	@extend .widthAndHeightAreFull;
	background-color: $pageDominantColor;
}

.middleArea {
	width: $pageWidth !important;
	height: 100%;
}
</style>
