<template>
	<div class="loginBox">
		<div class="login">
			<el-form :model="formInfor">
				<div class="title">登录</div>
				<div>
					<el-input
						v-model="formInfor.phone"
						class="input"
						placeholder="手机号"
					></el-input>
				</div>
				<div>
					<el-input
						v-model="formInfor.password"
						type="password"
						class="inputPas"
						placeholder="密码"
					></el-input>
				</div>
				<div>
					<el-button class="inputAndButton" @click="login">登录</el-button>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			formInfor: {
				phone: "",
				password: "",
			},
		};
	},
	methods: {
		login() {
			// console.log(this.formInfor);
			this.PushLogin();
		},
		//登录请求
		async PushLogin() {
			let res = await this.$http.login(this.formInfor);
			// console.log(res);
			if (res.code == 200) {
				this.$message.success(res.msg);
				//存储token
				localStorage.setItem("token", res.data.token);
				this.$publicFn.redirectToRoute("index");
			}
			if (res.code == 100) {
				this.$message.error(res.msg);
			}
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
.loginBox {
	//添加背景图片
	width: 100%;
	height: 100%;
	background-image: url("@/assets/image/BgLogin.jpg");
	//设置图片居中撑开屏幕
	background-position: center;
	background-size: cover;
	//设置背景图片固定
	background-attachment: fixed;
	//设置背景图片不重复
	background-repeat: no-repeat;
	//设置背景图片不滚动
	background-attachment: fixed;
	.login {
		position: absolute;
		@include display-flex();
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 25rem;
		height: 21.875rem;
		// background-color: red;
		border: 0.125rem solid rgba($color: #fff, $alpha: 0.5);
		border-radius: $borderRadius;
		margin: 0 auto;
		color: #fff;
		backdrop-filter: blur(0.1875rem);
		.title {
			text-align: center;
			font: {
				size: 1.875rem;
				weight: 700;
			}
		}
		button {
			width: 100%;
			color: #000;
			font: {
				weight: 700;
			}
		}
		.input {
			width: 18.75rem;
		}
		.inputPas {
			width: 18.75rem;
			margin: 1.25rem 0;
		}
	}
}
</style>
