<template>
	<div class="outerBox">
		<div
			class="list"
			v-for="(item, index) in menu"
			:key="index"
			@click="$publicFn.redirectToRoute(item.routerName)"
		>
			<img :src="require(`../../assets/image/IconImg/${item.icon}`)" />
			<p class="itemText">{{ item.name }}</p>
		</div>
	</div>
</template>

<script>
import menuData from "@/assets/Data/menu";
// console.log(menuData);
export default {
	data() {},
	mounted() {},
	computed: {
		//获取菜单
		menu() {
			return menuData[0].children;
		},
	},
	methods: {},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
.outerBox {
	@extend .contentBoxStyle;
	@extend .hideText;
	box-sizing: border-box;
	.list {
		@include cursor-pointer();
		@include display-flex();
		padding-top: 1.875rem;
		@extend .hideText;
		img {
			width: 1.25rem;
			height: 1.25rem;
		}
		.itemText {
			margin-left: 0.3125rem;
			font-size: 1rem;
		}
	}
}
</style>
