<template>
	<div>
		<div class="contentStyle">
			<!-- 标题 -->
			<h1 class="title">{{ articleContent.title }}</h1>
			<!-- 信息 -->
			<div class="information">
				<span>作者: {{ articleContent.nickname }}</span>
				<span
					>{{
						$publicFn.convertToMillion(parseInt(articleContent.see_num))
					}}
					浏览量</span
				>

				<span>发布时间: {{ articleContent.create_time }}</span>
			</div>
			<!-- 内容 -->
			<div class="contentText">
				<div class="text">{{ articleContent.content }}</div>
				<div class="text">{{ articleContent.content }}</div>
			</div>
			<!-- 点赞和踩 -->
			<div class="zanAndCai">
				<el-button
					:class="selectedZan ? 'selected' : 'default'"
					@click="zan(articleContent.id)"
					>赞</el-button
				>
				<el-button
					:class="selectedCai ? 'selected' : 'default'"
					@click="cai(articleContent.id)"
					>踩</el-button
				>
			</div>
			<!-- 评论 -->
			<div class="comments">
				<h2>该文章共有{{ commentsNum }}条评论</h2>
				<!--  -->
				<!-- 评论输入框 -->
				<!-- 用户发布评论 -->
				<div class="userPushComments">
					<div class="input">
						<el-input
							placeholder="请输入评论内容"
							type="textarea"
							:rows="2"
							v-model="myComment"
						></el-input>
						<el-button @click="PushComments">确定评论</el-button>
					</div>
				</div>
				<!-- 评论列表 -->
				<template v-for="(item, index) in commentsList" :key="index">
					<div class="commentItem">
						<div class="userInfor">
							<img :src="`${this.$store.state.baseURL}${item.avatar}`" />
							<div class="userBox">
								<div class="nickname">
									{{ item.nickname }}
								</div>
								<div class="time">
									{{ $publicFn.dateConversion(item.create_time) }}
								</div>
							</div>
						</div>
						<div class="itemContent">
							{{ item.content }}
						</div>
					</div>
				</template>
				<!-- <div v-if="commentsLength > this.showCommentsNum">
					<el-button @click="pushCommentsList" style="width: 100%"
						>加载更多评论</el-button
					>
				</div> -->
			</div>
		</div>
	</div>
</template>

<script>
export default {
	computed: {},
	data() {
		return {
			//内容
			tempContent: {},
			//文章内容
			articleContent: {},
			//时间
			time: "",
			//选中赞
			selectedZan: false,
			// 选中踩
			selectedCai: false,
			//评论列表
			commentsList: [],
			//显示评论列表
			showCommentsList: [],
			//我的评论
			myComment: "",
			//显示的评论数量
			showCommentsNum: 4,
			//评论长度
			commentsLength: 0,
			//用户信息
			userInfor: {},
			//评论数量
			commentsNum: 0,
		};
	},
	methods: {
		//点赞
		zan(id) {
			this.selectedZan = !this.selectedZan;
			this.selectedCai = false;
			let token = this.$publicFn.getToken();
			let value = { token: token, article_id: id, moduleName: "zan" };
			this.addZan(value);
		},
		//踩
		cai(id) {
			this.selectedCai = !this.selectedCai;
			this.selectedZan = false;
			let token = this.$publicFn.getToken();
			let value = { token: token, article_id: id, moduleName: "cai" };
			this.addCai(value);
		},
		//初始化cai和zan
		initCaiAndZan() {
			let token = this.$publicFn.getToken();

			let value = {
				token: token,
				article_id: this.articleContent.id,
				moduleName: "zan",
			};
			this.selectCaiAndZan(value);
			value = {
				token: token,
				article_id: this.articleContent.id,
				moduleName: "cai",
			};
			this.selectCaiAndZan(value);
		},
		//查询用户的cai和zan的情况
		async selectCaiAndZan(value) {
			let res = await this.$http.selectUserLike(value);
			if (value.moduleName == "zan") {
				this.selectedZan = res.likeTheColor;
			}
			if (value.moduleName == "cai") {
				this.selectedCai = res.nolikeTheColor;
			}
		},
		//增加点赞
		async addZan(value) {
			let res = await this.$http.insertLike(value);
			if (res.code == 200) {
				this.$message({
					message: res.msg,
					type: "success",
				});
			}
			if (res.code == 100) {
				this.$message({
					message: res.msg,
					type: "error",
				});
			}
		},
		// 增加踩
		async addCai(value) {
			let res = await this.$http.addCai(value);
			if (res.code == 200) {
				this.$message({
					message: res.msg,
					type: "success",
				});
			}
			if (res.code == 100) {
				this.$message({
					message: res.msg,
					type: "error",
				});
			}
		},
		//请求文章内容
		async getArticleContent(contentID) {
			let res = await this.$http.SelectContent(contentID);
			this.tempContent = res.data[0];
		},
		//请求评论
		async selectComment(create_id) {
			let res = await this.$http.selectComment(create_id);
			// console.log(res);
			if (res.code == 200) {
				this.commentsList = res.data;
				this.commentsNum = this.commentsList.length;
			}
			if (res.code == 100) {
				this.commentsNum = 0;
			}
			// this.showCommentsList = this.commentsList.splice(0, this.showCommentsNum);
			// console.log(this.showCommentsList);
			// this.commentsLength = this.commentsList.length;
		},
		//发布评论
		async PushComments() {
			let token = this.$publicFn.getToken();
			if (this.myComment == "") {
				this.$message({
					message: "评论内容不能为空",
					type: "error",
				});
				return;
			}
			let value = {
				token: token,
				article_id: this.articleContent.id,
				content: this.myComment,
			};
			let res = await this.$http.insertComment(value);
			if (res.code == 200) {
				this.$message({
					message: res.msg,
					type: "success",
				});
				this.myComment = "";
				this.selectComment(this.$route.params.contentID);
			}
			if (res.code == 100) {
				this.$message({
					message: res.msg,
					type: "error",
				});
			}
		},
		//添加更多评论
		pushCommentsList() {
			// console.log(this.commentsList);
			// this.showCommentsList.push(
			// 	this.commentsList.splice(this.showCommentsNum, this.showCommentsNum + 2)
			// );
			// this.showCommentsNum += 2;
		},
	},
	computed: {},
	mounted() {
		//请求文章内容
		this.getArticleContent(this.$route.params.contentID);
		//请求评论
		this.selectComment(this.$route.params.contentID);
	},
	created() {},
	watch: {
		tempContent(newValue, oldValue) {
			newValue.create_time = this.$publicFn.dateConversion(
				newValue.create_time
			);
			this.articleContent = newValue;
			//初始化cai和zan
			this.initCaiAndZan();
		},
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
$marginTB: 0.625rem 0;
.contentStyle {
	width: 100%;
	padding: 1.25rem;
	box-sizing: border-box;
	// background-color: red;
	.title {
		width: 100%;
		@extend .hideText;
	}
	.information {
		span {
			margin-left: 0.625rem;
			color: $pageGrayColor;
			font: {
				size: 0.75rem;
			}
		}
	}
	.contentText {
		.text {
			font: {
				size: 1rem;
			}
			line-height: 2.5rem;
		}
	}
	.zanAndCai {
		@include display-flex($justify: right);
		.selected {
			background-color: rgba($color: #f46f6f, $alpha: 0.5);
			color: black;
		}
		.default {
			background-color: rgba($color: #fff, $alpha: 0.5);
			color: black;
		}
	}
	.comments {
		margin-top: 0.625rem;
		.userPushComments {
			.input {
				margin-top: 0.625rem;
				@include display-flex($direction: column, $align: left);
			}
		}

		.commentItem {
			//下边框阴影
			padding: 0.625rem;
			box-sizing: border-box;
			border-bottom: 0.0625rem solid #ccc;

			.userInfor {
				@include display-flex($justify: left);
				margin: $marginTB;
				img {
					width: 3.125rem;
					height: 3.125rem;
					border-radius: 50%;
				}
				.userBox {
					font: {
						size: 1.25rem;
					}
					margin-left: 0.625rem;
					.time {
						display: block;
						font: {
							size: 0.75rem;
						}
						margin-top: 0.3125rem;
						color: $pageGrayColor;
					}
				}
				// flex-direction: column;
			}
			.itemContent {
				@include text-overflow(3);
				font: {
					size: 1rem;
				}
			}
		}
	}
}
</style>
