import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import index from "../views/index/index.vue";
import personalCenter from "../views/personalCenter/personalCenter.vue";
import hotArticle from "../views/hotArticle/hotArticle.vue";
import myLike from "../views/myLike/myLike.vue";
import myDislike from "../views/myDislike/myDislike.vue";
import myComment from "../views/myComment/myComment.vue";
import myPublish from "../views/myPublish/myPublish.vue";
import login from "../views/login/login.vue";
import enroll from "../views/enroll/enroll.vue";
import articleDetails from "../views/articleDetails/articleDetails.vue";
import publishAnArticle from "../views/publishAnArticle/publishAnArticle.vue";

const routes: Array<RouteRecordRaw> = [
	{
		path: "/",
		name: "index",
		component: index,
	},
	{
		path: "/personalCenter",
		name: "personalCenter",
		component: personalCenter,
	},
	{
		path: "/hotArticle",
		name: "hotArticle",
		component: hotArticle,
	},
	{
		path: "/myLike",
		name: "myLike",
		component: myLike,
	},
	{
		path: "/myDislike",
		name: "myDislike",
		component: myDislike,
	},
	{
		path: "/myComment",
		name: "myComment",
		component: myComment,
	},
	{
		path: "/myPublish",
		name: "myPublish",
		component: myPublish,
	},
	{
		path: "/login",
		name: "login",
		component: login,
	},
	{
		path: "/enroll",
		name: "enroll",
		component: enroll,
	},
	{
		path: "/articleDetails/:contentID",
		name: "articleDetails",
		component: articleDetails,
	},
	{
		path: "/publishAnArticle",
		name: "publishAnArticle",
		component: publishAnArticle,
	},
];

const router = createRouter({
	history: createWebHashHistory(),
	routes,
});

export default router;
