import router from "@/router";
export default {
	//初始化页面高度
	setMiddleHeight() {
		// 获取页面高度
		const pageHeight: number = document.documentElement.clientHeight;

		// 获取顶部导航栏元素
		const topNavigationElement: HTMLElement | null =
			document.querySelector(".topNavigation");

		if (topNavigationElement) {
			// 获取顶部导航栏高度
			const topNavigationHeight: number = topNavigationElement.offsetHeight;

			// 设置轮播图插位符的高度
			const carouselMapSocketsElement: HTMLElement | null =
				document.getElementById("carouselMapSockets");

			if (carouselMapSocketsElement) {
				carouselMapSocketsElement.style.height = topNavigationHeight + "px";
			}

			// 计算中间内容显示的高度为页面高度减去顶部导航栏高度
			const middleHeight: number = pageHeight - topNavigationHeight;

			// 获取中间内容元素
			const middleElement: HTMLElement | null =
				document.querySelector(".middle");
			const leftElement: HTMLElement | null = document.querySelector(".left");
			const rightElement: HTMLElement | null = document.querySelector(".right");
			// console.log(middleElement, leftElement, rightElement);
			if (middleElement && leftElement && rightElement) {
				// 设置中间内容显示的高度的 CSS 样式
				middleElement.style.height = middleHeight + "px";
				leftElement.style.height = middleHeight + "px";
				rightElement.style.height = middleHeight + "px";
				// console.log("我被设置了");
			}
			if (carouselMapSocketsElement) {
				// 再次设置轮播图插位符的高度
				carouselMapSocketsElement.style.height = topNavigationHeight + "px";
			}
		}
	},
	// 设置文章分类列表显示的个数
	changeClassifyListList(): number {
		// 显示的分类列表选项个数
		let showClassifyListListNum: number = 0;
		// .classifyList盒子内容宽度
		let classifyListWidth: number =
			document.querySelector(".classifyList")!.clientWidth;
		// 获取.classifyList盒子的左右内边距
		let classifyListPaddingLeft: string = window.getComputedStyle(
			document.querySelector(".classifyList")!
		).paddingLeft;
		let classifyListPaddingRight: string = window.getComputedStyle(
			document.querySelector(".classifyList")!
		).paddingRight;
		// .classifyList盒子的左右内边距和
		let classifyListPadding: number =
			parseInt(classifyListPaddingLeft) + parseInt(classifyListPaddingRight);
		// .classifyList盒子的上下内边距和
		let classifyListPaddingTop: string = window.getComputedStyle(
			document.querySelector(".classifyList")!
		).paddingTop;
		let classifyListPaddingBottom: string = window.getComputedStyle(
			document.querySelector(".classifyList")!
		).paddingBottom;
		// 获取.item盒子的大小包括外边距
		let itemWidth: number = document.querySelector(".item")!.clientWidth;
		// 显示的分类列表选项个数=(.classifyList盒子内容宽度-.classifyList盒子的左右内边距和)/.item盒子的大小--向下取整
		showClassifyListListNum = Math.floor(
			(classifyListWidth - classifyListPadding) / itemWidth
		);
		return showClassifyListListNum;
	},
	//设置文章列表显示的个数
	getShowArticleListNum(): number {
		// 显示的文章列表个数
		let showArticleListNum: number = 0;

		// 获取.listItemStyle元素的高度
		let listItemStyleHeight: number =
			document.querySelector(".listItemStyle")!.clientHeight;

		// 获取.listOfArticles的高度
		let listOfArticlesHeight: number =
			document.querySelector(".listOfArticles")!.clientHeight;

		// 获取.listOfArticles的下外边距
		let listOfArticlesMarginBottom: number = parseInt(
			window.getComputedStyle(document.querySelector(".listOfArticles")!)
				.marginBottom
		);

		// 文章列表个数 = .listItemStyle元素的高度 / (.listOfArticles的下外边距 + .listOfArticles的高度) 向上取整
		showArticleListNum = Math.ceil(
			listItemStyleHeight / (listOfArticlesHeight + listOfArticlesMarginBottom)
		);

		return showArticleListNum;
	},

	//定义一个日期转换器在vue3中编写一个函数把这种格式的日期2023-06-07T07:01:56.000Z转换成2023-06-07-01:56
	dateConversion(date: string): string {
		// let dateArr: string[] = date.split("T");
		// return dateArr[0];
		const dateObj = new Date(date);
		const year = dateObj.getFullYear();
		const month = (dateObj.getMonth() + 1).toString().padStart(2, "0");
		const day = dateObj.getDate().toString().padStart(2, "0");
		const hour = dateObj.getHours().toString().padStart(2, "0");
		const minute = dateObj.getMinutes().toString().padStart(2, "0");
		return `${year}-${month}-${day}-${hour}:${minute}`;
	},
	//定义数字转换器
	convertToMillion(number: number): string {
		//输出亿
		if (number >= 100000000) {
			return (number / 100000000).toFixed(1) + "亿";
		}
		//输出万
		if (number >= 10000) {
			return (number / 10000).toFixed(1) + "万";
		}
		return number.toString();
	},
	//获取热门文章显示的个数
	getHotContentNum(): number {
		let listNum: number = 0;

		// 获取 popularArticles 的元素
		const popularArticles = document.querySelector(".popularArticles");

		// 获取 title 的元素
		const title = document.querySelector(".title");

		// 获取 popularList 的元素
		const popularList = document.querySelector(".popularList");

		if (popularArticles && title && popularList) {
			// 获取 popularArticles 的高度
			const popularArticlesHeight = popularArticles.clientHeight;

			// 获取 title 的高度
			const titleHeight = title.clientHeight;

			// 获取 title 盒子的下外边距
			const titleMargin = parseInt(window.getComputedStyle(title).marginBottom);

			// 获取 popularList 的高度
			const popularListHeight = popularList.clientHeight;
			// console.log("popularArticles 的高度", popularArticlesHeight);
			// console.log("title 的高度", titleHeight);
			// console.log("title 盒子的上下外边距", titleMargin);
			// console.log("popularList 的高度", popularListHeight);
			// 计算 listNum
			listNum =
				(popularArticlesHeight - titleHeight - titleMargin) /
				popularListHeight /
				2;

			//listNum 向下取整;
			// listNum = Math.floor(listNum);
			// listNum 向上取整;
			listNum = Math.ceil(listNum);
			// console.log("listNum", listNum);
		}

		return listNum;
	},
	//初始化页面布局
	async initLayout(): Promise<object> {
		const infoObj = {
			showClassifyNumber: 0,
			showArticleNumber: 0,
			showHotContentNumber: 0,
		};
		this.setMiddleHeight();
		const stopInItem = new Promise<void>(resolve => {
			const interval = setInterval(() => {
				if (document.getElementsByClassName("item").length > 0) {
					infoObj.showClassifyNumber = this.changeClassifyListList();
					// console.log("停止时钟stopInItem");
					clearInterval(interval);
					resolve();
				}
			}, 500);
		});
		const stopInList = new Promise<void>(resolve => {
			const interval = setInterval(() => {
				if (document.getElementsByClassName("listOfArticles").length > 0) {
					infoObj.showArticleNumber = this.getShowArticleListNum();
					clearInterval(interval);
					resolve();
				}
			}, 1000);
		});
		const stopPopularList = new Promise<void>(resolve => {
			const interval = setInterval(() => {
				if (document.getElementsByClassName("popularList").length > 0) {
					// console.log(this.getHotContentNum());
					infoObj.showHotContentNumber = this.getHotContentNum();
					// console.log("停止时钟stopPopularList");
					clearInterval(interval);
					resolve();
				}
			}, 1000);
		});
		await Promise.all([stopInItem, stopInList, stopPopularList]);
		return infoObj;
	},

	//初始化页面布局单页面
	async initLayoutSinglePage(): Promise<object> {
		const infoObj = {
			showClassifyNumber: 0,
			showArticleNumber: 0,
		};
		this.setMiddleHeight();
		const stopInItem = new Promise<void>(resolve => {
			const interval = setInterval(() => {
				if (document.getElementsByClassName("item").length > 0) {
					infoObj.showClassifyNumber = this.changeClassifyListList();
					// console.log("停止时钟stopInItem");
					clearInterval(interval);
					resolve();
				}
			}, 500);
		});
		const stopInList = new Promise<void>(resolve => {
			const interval = setInterval(() => {
				if (document.getElementsByClassName("listOfArticles").length > 0) {
					infoObj.showArticleNumber = this.getShowArticleListNum();
					clearInterval(interval);
					resolve();
				}
			}, 1000);
		});

		await Promise.all([stopInItem, stopInList]);
		return infoObj;
	},

	//定义跳转函数根据路由name进行跳转
	redirectToRoute(routeName: string) {
		const route = router.options.routes.find(r => r.name === routeName);
		if (route) {
			router.push({ name: route.name });
		} else {
			console.error(`Route with name "${routeName}" not found.`);
		}
	},
	//获取本地token
	getToken(): string | null {
		return localStorage.getItem("token");
	},
	//获取当前页面的router中的name
	getCurrentRouteName(): any {
		return router.currentRoute.value.name;
	},
	//清空本地token
	removeToken(): void {
		localStorage.removeItem("token");
	},
	// 跳转文章详情
	jumpToArticleDetails(contentID: any) {
		// console.log(contentID);
		router.push({
			name: "articleDetails",
			params: {
				contentID: contentID,
			},
		});
	},
};
