import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

const app = createApp(App);

//定义全局请求根路径
const baseURL = "http://localhost:3000";
// 引入 ElementPlus 插件(npm i element-plus)
import ElementPlusPlugin from "@/plugins/element-plus";

// 全局注册 ElementPlus 图标组件(npm install @element-plus/icons-vue)
import * as ElementPlusIcons from "@element-plus/icons-vue";
for (const [key, component] of Object.entries(ElementPlusIcons)) {
	app.component(key, component);
}

// 引入HTTP请求工具并配置为全局方法
import axios from "axios";
import UserManage_Api from "@/api/UserManage/index";
app.config.globalProperties.$http = {
	...UserManage_Api,
};
app.config.globalProperties.$axios = axios;

//引入全局函数库
import publicFn from "./assets/script/publicFn";
app.config.globalProperties.$publicFn = publicFn;

//引入公共组件
import CommonBody from "./components/common/commonBody.vue";
import TopNavigation from "./components/common/topNavigation.vue";
import CarouselMap from "./components/common/carouselMap.vue";
import LeftNavigation from "./components/common/leftNavigation.vue";
import RightContent from "./components/common/rightContent.vue";
import IntermediateArticleArea from "./components/common/intermediateArticleArea.vue";
import ListOfArticles from "./components/common/listOfArticles.vue";

app.use(store).use(router).use(ElementPlusPlugin);

//注册全局组件
app.component("CommonBody", CommonBody);
app.component("TopNavigation", TopNavigation);
app.component("CarouselMap", CarouselMap);
app.component("LeftNavigation", LeftNavigation);
app.component("RightContent", RightContent);
app.component("IntermediateArticleArea", IntermediateArticleArea);
app.component("ListOfArticles", ListOfArticles);

app.mount("#app");
