<template>
	<div>
		<CommonBody>
			<template v-slot:topNavigation>
				<TopNavigation></TopNavigation>
			</template>
			<template v-slot:carouselMap>
				<CarouselMap></CarouselMap>
			</template>
			<template v-slot:left>
				<LeftNavigation></LeftNavigation>
			</template>
			<template v-slot:middle>
				<IntermediateArticleArea
					:showClassifyNumber="showClassifyNumber"
					:showArticleNumber="showArticleNumber"
					:articleList="articleList"
					class="middleArea"
					@updateArticleList="updateArticleList"
				></IntermediateArticleArea>
			</template>

			<template v-slot:right>
				<RightContent
					:showHotContentNumber="showHotContentNumber"
				></RightContent>
			</template>
		</CommonBody>
	</div>
</template>

<script>
export default {
	name: "Index",
	data() {
		return {
			//显示分类的个数
			showClassifyNumber: 11,
			//显示文章的个数
			showArticleNumber: 5,
			///显示热门文章的个数
			showHotContentNumber: 5,
			//文章列表
			articleList: [],
		};
	},
	methods: {
		// 初始化页面布局
		initLayout() {
			this.$publicFn.initLayout().then(res => {
				this.showClassifyNumber = res.showClassifyNumber;
				this.showArticleNumber = res.showArticleNumber;
				this.showHotContentNumber = res.showHotContentNumber;
			});
		},
		//请求文章列表
		async getArticleList() {
			let articleList = await this.$http.selectArticleList();
			this.articleList = articleList.data;
		},
		//根据分类id获取文章列表
		updateArticleList(newArticleList) {
			// console.log('newArticleList', newArticleList);
			this.articleList = [];
			this.articleList = newArticleList;
			// console.log('this.articleList', this.articleList);
		},
	},
	mounted() {
		this.initLayout();
		window.onresize = () => {
			return (() => {
				this.initLayout();
			})();
		};
		//请求文章列表
		this.getArticleList();
	},
};
</script>

<style lang="scss" scoped>
.middleArea {
	// width: 50rem!important;
	height: 100%;
}
</style>
