<template>
	<div id="displayArea" @scroll="handleScroll">
		<template v-for="(item, index) in showArticleList">
			<div class="listOfArticles">
				<div
					class="itemTitle"
					@click="this.$publicFn.jumpToArticleDetails(item.id)"
				>
					{{ item.title }}
				</div>
				<div class="itemInformation">
					<div>作者: {{ item.nickname }}</div>
					<div>
						{{ $publicFn.convertToMillion(parseInt(item.like_num)) }}点赞
					</div>
					<div>
						{{ $publicFn.convertToMillion(parseInt(item.see_num)) }}浏览
					</div>
					<div>发布时间: {{ $publicFn.dateConversion(item.create_time) }}</div>
				</div>
				<div class="itemContent">
					{{ item.content }}
				</div>
			</div>
		</template>
	</div>
</template>
<script>
export default {
	props: {
		articleList: {
			type: Array,
			default: () => [],
		},
		showArticleNumber: {
			type: Number,
			default: 5,
		},
	},
	computed: {},
	data() {
		return {
			//滚动到底部标记
			scrolledToBottom: false,
			// 存储显示的文章列表
			showArticleList: [],
			// 显示文章列表的个数
			showList: this.showArticleNumber,
			articleListChild: this.articleList,
		};
	},
	computed: {},
	methods: {
		//监听列表显示区域的滚动事件
		handleScroll(event) {
			const displayArea = event.target;
			// 计算滚动条距离底部的距离
			const distanceFromBottom =
				displayArea.scrollHeight -
				(displayArea.scrollTop + displayArea.clientHeight);

			// 当滚动条滚动到底部且消息未被打印时，打印消息并更新状态
			if (distanceFromBottom <= 1 && !this.scrolledToBottom) {
				// console.log("滚动到底了");
				this.scrolledToBottom = true;
				//加载更多文章
				this.loadMoreArticle();
			}
			//向上滚动时恢复scrolledToBottom=false,
			if (distanceFromBottom > 1 && this.scrolledToBottom) {
				// console.log("向上滚动");
				this.scrolledToBottom = false;
			}
		},
		//定义一个方法，加载更多文章
		loadMoreArticle() {
			// 向showArticleList添加articleList下标showListNum开始的两个元素
			this.showArticleList.push(
				...this.articleListChild.slice(this.showList, this.showList + 2)
			);
			// 更新showListNum
			this.showList += 2;
		},
	},
	mounted() {
		//初始文章列表
		this.showArticleList.push(...this.articleListChild.slice(0, this.showList));
	},
	created() {},
	watch: {
		showArticleNumber(newValue, oldValue) {
			// console.log(newValue, oldValue);
			this.showList = newValue;
			this.showArticleList = [];
			this.showArticleList.push(...this.articleListChild.slice(0, newValue));
			// console.log(this.showArticleList);
		},
		articleList(newValue, oldValue) {
			//清空id=displayArea的滚动条,使其顶部显示
			document.getElementById("displayArea").scrollTop = 0;
			this.articleListChild = newValue;
			// console.log(this.articleListChild );
			this.showList = this.showArticleNumber;
			//初始话文章列表
			this.showArticleList = [];
			this.showArticleList.push(
				...this.articleListChild.slice(0, this.showList)
			);
			// console.log(this.showArticleList);
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";

#displayArea {
	// 允许滚动
	overflow-y: auto;
}
.listOfArticles {
	box-sizing: border-box;
	padding: 0.625rem 1.25rem;
	width: 100%;
	height: 8.75rem;
	background-color: $pageDominantColor;
	border-radius: $borderRadius;
	margin-bottom: 0.625rem;
	.itemTitle {
		@include cursor-pointer();
		// background-color:red ;
		height: 2.5rem;
		line-height: 2.5rem;
		width: 18.75rem;
		@extend .hideText;
		font: {
			size: 1.375rem;
			weight: bold;
		}
	}
	.itemInformation {
		// background-color: blue;
		height: 1.25rem;
		box-sizing: border-box;
		@extend .hideText;
		color: $pageGrayColor;
		div {
			@extend .hideText;
			// width: 6.25rem;
			margin-right: 0.625rem;
			// background-color: red;
			// background-color: blanchedalmond;
			font: {
				size: 0.75rem;
			}
			line-height: 1.25rem;
			display: inline-block;
		}
		div:last-child {
			width: 9.375rem;
		}
	}
	.itemContent {
		// background-color: brown;
		height: 3.75rem;
		line-height: 1.875rem;
		box-sizing: border-box;
		// 设置文字只显示两行
		@include text-overflow(2);
	}
}
</style>
