<template>
	<div>
		<commonBody>
			<template v-slot:topNavigation>
				<TopNavigation></TopNavigation>
			</template>
			<template v-slot:middle>
				<div class="publish">
					<!-- 输入title -->
					<h2 class="title">文章标题</h2>
					<el-input
						class="input"
						placeholder="请输入文章标题"
						v-model="title"
					></el-input>
					<!-- 输入title -->
					<h2 class="title">文章内容</h2>
					<el-input
						class="input"
						placeholder="请输入文章内容"
						type="textarea"
						:rows="24"
						v-model="content"
					></el-input>
					<!-- 选择分类 -->
					<h2 class="input">文章分类</h2>
					<el-select v-model="cate_id" placeholder="请选择">
						<el-option
							v-for="item in classifyList"
							:key="item.id"
							:label="item.name"
							:value="item.id"
						>
						</el-option>
					</el-select>
					<div>
						<el-button @click="publish" style="width: 100%; margin-top: 1.25rem"
							>发布</el-button
						>
					</div>
				</div>
			</template>
		</commonBody>
	</div>
</template>

<script>
export default {
	data() {
		return {
			classifyList: [],
			// 选择的分类
			cate_id: "",
			// 文章标题
			title: "",
			// 文章内容
			content: "",
		};
	},
	methods: {
		// 初始化页面布局
		initLayout() {
			this.$publicFn.initLayoutSinglePage();
		},
		//请求文章分类列表
		async getArticleListByClassify() {
			let classifyList = await this.$http.selectCategorizedList();
			this.classifyList = classifyList.data;
			//不要name==全部的分类
			this.classifyList = this.classifyList.filter(item => item.name != "全部");
		},
		async publish() {
			if (this.title == "") {
				this.$message.error("请输入文章标题");
				return;
			}
			if (this.content == "") {
				this.$message.error("请输入文章内容");
				return;
			}
			if (this.cate_id == "" || this.cate_id == undefined) {
				this.$message.error("请选择文章分类");
				return;
			}
			let token = this.$publicFn.getToken();
			let value = {
				title: this.title,
				content: this.content,
				cate_id: this.cate_id,
				token: token,
			};
			let res = await this.$http.insertIntoRelease(value);
			if (res.code == 200) {
				this.$message.success("发布成功");
				//跳转到我的发布
				// this.$router.push("/home");
			}
			if (res.code == 100) {
				this.$message.error("发布失败");
			}
		},
	},
	mounted() {
		this.initLayout();
		window.onresize = () => {
			return (() => {
				this.initLayout();
			})();
		};
		this.getArticleListByClassify();
	},
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/commonStyle.scss";
@import "@/assets/scss/commonStyle.scss";

.publish {
	// @extend .widthAndHeightAreFull;
	width: 120%;
	height: 100%;
	background-color: $pageDominantColor;
	border-radius: $borderRadius;
	padding: 1.25rem;
	box-sizing: border-box;
	// 允许滚动，隐藏超出部分，隐藏滚动条
	overflow: auto;
	overflow-x: hidden;
	overflow-y: auto;
	&::-webkit-scrollbar {
		width: 0;
		height: 0;
	}

	&::-webkit-scrollbar-thumb {
		background-color: transparent;
	}
	.title {
		margin-top: 1.25rem;
	}
	.input {
		margin-top: 0.625rem;
	}
}
</style>
